import React from 'react'
import '../styles/header.css'
import { observer, inject} from 'mobx-react'

@inject('userStore')
@observer
class Header extends React.Component {
    render() {
        const { userStore } = this.props
        const { usermail } = userStore
        return <header>
            <h1>MY FAVORITE MOVIE</h1>
            <div className="bottom">
                <h4>{usermail}</h4>
                <p>
                This is my sixth project and the first work <br />
                I've implemented as a React. <br />
                I put the most impressive movie that I have ever seen.<br />
                Although it is a YouTube video, <br />
                I am trying to add more and more works.<br />
                </p>
            <nav>
                <ul>
                    <li>유튜브</li>
                    <li>페이스북</li>
                    <li>인스타그램</li>
                    <li>이메일</li>
                    <li>블로그</li>
                    {/* 하이퍼링크 하려면 라우터로 */}
                </ul>
            </nav>
            </div>
        </header>
    }
}

export default Header